// Categories

.categories {
  position: relative;
  line-height: 24px;
  background-color: #f9f9f9;

  @include tablet {
    line-height: 30px;

    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 87%;
      width: 100%;
      height: 24px;
      background-image: url("..//..//img/zigzag-fill.svg");
      background-position-x: center;
    }
  }

  @include desktop {
    &::after {
      top: 82%;
      height: 34px;
      background-size: 1250px;
    }
  }
}

.categories .wrapper {
  padding: 0;

  @include tablet {
    padding: 0 50px;
  }
}


// List

.categories__list {
  @include overlay;
  font-size: 19px;

  @include tablet {
    font-size: 20px;
    display: flex;
    flex-wrap: wrap;
  }
}

// Item

.categories__item {
  font-weight: 700;

  @include tablet {
    width: 50%;
  }
}

// Link

.categories__link {
  position: relative;
  display: block;
  padding: 24px 160px 31px 30px;
  text-decoration: none;
  color: $basic-white;
  background-color: $basic-green;
  cursor: pointer;
  transform: translateY(0);

  @include tablet {
    padding: 25px 167px 34px 36px;
  }

  @include desktop {
    padding: 25px 350px 34px 36px;
  }

  &::after {
    content: "";
    opacity: 0.5;
    position: absolute;
  }

  &--interior {
    &::after {
      background-image: url("..//..//img/interior.svg");
      width: 40px;
      height: 62px;
      right: 36px;
      top: 24px;
    }
  }

  &--toy {
    background-color: $basic-dark-green;

    &::after {
      background-image: url("..//..//img/toy.svg");
      width: 60px;
      height: 60px;
      top: 20px;
      right: 28px;
    }
  }

  @media (any-hover: hover) {
    &:hover {
      transform: translateY(-5px);
      transition: 200ms;
    }

    &:active {
      transform: translateY(-5px);
    }
  }
}
